<template>
  <div
    class="card-body overflow-y-scroll p-4"
    style="height: 65vh"
    ref="items"
    id="items"
  >
    <template v-for="(item, index) in choseList" :key="index">
      <div
        class="card mb-4 shadow btn animate__animated animate__infinite headShake"
        :id="item.id"
      >
        <div class="row g-0">
          <div class="col-md-4">{{ index + 1 }}</div>
          <div class="col-md-8">
            <div class="card-body">
              <h5 class="card-title">Card title</h5>
              <div class="card-text">
                {{ item.title }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
  <div class="card-footer">
    <button
      class="btn btn-outline-primary"
      @click="this.$refs.addTopic.showModal()"
    >
      
    </button>
  </div>
  <AddTopicModal ref="addTopic" />
</template>

<style lang="scss">
.animate__animated.animate__headShake {
  --animate-duration: 1.5s;
  --animate-delay: 0s;
}
</style>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import Sortable from 'sortablejs';
import AddTopicModal from '@/components/group/AddTopicModal.vue';
import choseData from '@/assets/jsons/chose.json';

export default {
  data() {
    return {
      sortable: '',
      choseList: choseData,
      newList: '',
    };
  },
  components: {
    AddTopicModal,
  },
  mounted() {
    this.sortable = Sortable.create(this.$refs.items, {
      animation: 500,
      onStart() {
        const headShakeList = Object.values(
          // eslint-disable-next-line comma-dangle
          document.getElementsByClassName('headShake')
        );
        headShakeList.forEach((item) => {
          item.classList.add('animate__headShake');
        });
      },
      onEnd() {
        const headShakeList = Object.values(
          // eslint-disable-next-line comma-dangle
          document.getElementsByClassName('headShake')
        );
        headShakeList.forEach((item) => {
          item.classList.remove('animate__headShake');
        });
        const tempList = document.getElementsByClassName('headShake');
        this.newList = [];
        Object.values(tempList).forEach((item) => {
          this.newList.push(item.id);
        });
      },
    });
  },
};
</script>
